<template>
  <!-- 修改应用信息 -->
  <el-dialog class="app_detail_update_appinfo" title="修改应用介绍" :visible.sync="dialogVisible.dialogVisible" :before-close="beforeClose" width="30%">
    <!-- 修改2 -->
    <div class="app_cover">
      <div class="app_cover_content">
        <el-input type="textarea" :rows="6" resize="none" placeholder="请输入应用介绍" v-model="dialogVisible.description"></el-input>
        <!-- <textarea placeholder="请输入应用介绍" class="app_cover_content_change_textarea" name="" id="" cols="30" rows="10"></textarea> -->
      </div>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button @click="beforeClose">取 消</el-button>
      <el-button type="primary" @click="submitChange" :loading="dialogVisible.loadding">保 存</el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  props: {
    dialogVisible: { // 是否显示
      type: Object,
      required: true
    }
  },
  data() {
    return {
      typeList: []
    }
  },
  methods: {
    // 确定修改
    submitChange() {
      this.dialogVisible.loadding = true;
      this.dialogVisible.isUpload = true;
      // 把保存事件发送给父级
      this.$emit('submit');
    },

    /**
     * 关闭弹出层的回调 逻辑
     *  判断当前是否是正在上传保存的信息，如果是，则不隐藏，
     *  如果不是 则隐藏
     */
    beforeClose() {
      console.log('关闭钱的糊掉')
      if (this.dialogVisible.isUpload) {
        console.log('不能关闭');
        return;
      }
      console.log('可以关闭');
      this.dialogVisible.dialogVisible = false;
    }
  }
}
</script>


<style>
/* 修改 start */
.app_cover_content {
  padding: 0 20px;
}
.app_cover_content_change_textarea {
  box-sizing: border-box;
  width: 100%;
  height: 100px;
  margin: 30px 0 100px 0;
  padding: 10px 6px;
  border: 1px solid #e6e6e6;
  font-size: 14px;
  resize: none;
  outline: none;
  color: #333333;
}
</style>
