<template>
  <!-- 修改应用信息 -->
  <el-dialog class="app_detail_update_appinfo" title="应用主页" :visible.sync="dialogVisible.dialogVisible" :before-close="beforeClose" width="30%">
    <!-- 修改2 -->
    <div class="app_cover">
      <div class="app_cover_content">
        <div class="app_cover_content_top">
          <div class="app_c_content_des app_c_content_des1">
            <p>输入应用主页：</p>
            <input type="text" v-model="dialogVisible.url" placeholder="请输入应用主页" />
          </div>
        </div>
      </div>
    </div>

    <span slot="footer" class="dialog-footer">
      <el-button @click="beforeClose">取 消</el-button>
      <el-button type="primary" @click="submitChange" :loading="dialogVisible.loadding">保 存</el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  props: {
    dialogVisible: { // 是否显示
      type: Object,
      required: true
    }
  },
  methods: {
    // 确定修改
    submitChange() {
      this.dialogVisible.loadding = true;
      this.dialogVisible.isUpload = true;
      // 把保存事件发送给父级
      this.$emit('submit');
    },

    /**
     * 关闭弹出层的回调 逻辑
     *  判断当前是否是正在上传保存的信息，如果是，则不隐藏，
     *  如果不是 则隐藏
     */
    beforeClose() {
      console.log('关闭钱的糊掉')
      if (this.dialogVisible.isUpload) {
        console.log('不能关闭');
        return;
      }
      console.log('可以关闭');
      this.dialogVisible.dialogVisible = false;
    }
  }
}
</script>


<style>
/* 应用主页 start*/
.app_cover {
  /* width: 640px; */
  margin: 20px auto;
  background: #fff;
}
.app_cover_content {
  padding: 0 20px;
}
.app_cover_content_top {
  border-bottom: 1px solid #e6e6e6;
}
.app_c_content_des {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 0 35px 0;
}
.app_c_content_des p {
  width: 100px;
  color: #999;
}
.app_c_content_des input {
  /* width: 505px; */
  height: 40px;
  border: 1px solid #e6e6e6;
  text-indent: 20px;
}
.app_c_content_des1 input {
  width: 400px;
}
/* 应用主页 end */
</style>
