<template>
  <div class="c_view_range">
    <div class="cwr_title">可见范围</div>
    <div>
      <div class="cwr_list clearfix">

        <!-- 已选部门列表 -->
        <div class="cwr_item" v-for="item in powerList" :key="item.depid">
          <img v-if="item.type == 'dep'" :src="require('../../../assets/img/appManage/department.png')" alt="">
          <img v-if="item.type == 'user'" :src="require('../../../assets/img/appManage/man.png')" alt="">
          <span>{{item.name}}</span>
        </div>

        <p class="cwr_btn fl" @click="add">{{msgCon}}</p>
      </div>

    </div>
    <MaskLayer :dialogShow.sync="dialogShow" title="可见范围" twoTitle="此应对以下用户可见" @submit="submit" @reset="reset" :powerList="vModel_powerList" />
  </div>
</template>
<script>
import MaskLayer from '../../../components/c_maskLayer'
export default {
  props: ['alreadySelect'],

  data() {
    return {
      dialogShow: false,
      msg: '修改',
      powerList: [], // 当前展示的
      vModel_powerList: [], // 双向绑定的
    }
  },
  computed: {
    msgCon(){
      if(this.powerList.length === 0){
        return '新增';
      }else{
        return '修改';
      }
    }
  },
  methods: {
    // 点击修改按钮
    add() {
      console.log('点击了修改按钮');
      this.dialogShow = true;
    },

    // 监听到的保存按钮
    submit(){
      // console.log('保存');
      // console.log(this.powerList);
      // console.log(this.vModel_powerList);
      // return ;
      this.powerList = this.vModel_powerList;
      this.dialogShow = false;
      this.$emit('allow_ranges', this.powerList);
    },

    // 监听到的提交按钮
    reset(){
      console.log('重置');
      this.vModel_powerList = this.powerList;
    }
  },

  created(){
    console.log('传递过来的');
    console.log(this.alreadySelect);
  },

  components: {
    MaskLayer
  }
}
</script>
<style scoped>
.c_view_range{
  padding-bottom: 40px;
  border-bottom: 1px solid #e6e6e6;
}
.cwr_title {
  padding-top: 40px;
  font-weight: bold;
}

.cwr_list{
  padding-top: 20px;
}
.cwr_item {
  float: left;
}
.cwr_item {
  display: flex;
  align-items: center;
  height: 30px;
  padding: 0 10px;
  margin-right: 8px;
  line-height: 30px;
  background: #2f78ef;
  color: #fff;
}
.cwr_item img {
  width: 14px;
  height: 14px;
  margin-right: 5px;
}
.cwr_btn {
  line-height: 30px;
  color: #2f78ef;
  cursor: pointer;
}
</style>
