<template>
  <!-- 修改应用信息 -->
  <el-dialog class="app_detail_update_appinfo" title="修改应用信息" :visible.sync="dialogVisible.dialogVisible" :before-close="beforeClose" width="30%">
    <!-- 修改2 -->
    <div class="app_cover">
      <div class="app_cover_content">
        <div class="app_cover_content_logo">
          <p class="app_cover_content_l_des">应用logo：</p>
          <div class="app_cover_content_logowrap">
            <span>LOGO</span>
            <img :src="dialogVisible.logoUrl" alt="">
            <input type="file" @input="logoChange">
          </div>
          <p class="app_cover_content_des">建议使用750*750，1M以内的JPG、PNG图片</p>
        </div>
        <div class="app_cover_content_name">
          <p class="app_cover_content_l_des">应用名称：</p>
          <input type="text" v-model="dialogVisible.appName" placeholder="请输入应用名称" />
        </div>
        <div class="app_cover_content_type">
          <p class="app_cover_content_l_des">所属分类：</p>
          <div class="app_cover_content_type_wrap">
            <el-radio v-model="dialogVisible.typeid" v-for="item in typeList" :key="item._id" :label="item._id">{{item._name}}</el-radio>
            <!-- <el-radio v-model="dialogVisible.typeid" label="1">备选项</el-radio>
            <el-radio v-model="dialogVisible.typeid" label="2">备选项</el-radio> -->
          </div>
        </div>
      </div>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button @click="beforeClose">取 消</el-button>
      <el-button type="primary" @click="submitChange" :loading="dialogVisible.loadding">保 存</el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  props: {
    dialogVisible: { // 是否显示
      type: Object,
      required: true
    }
  },
  data() {
    return {
      typeList: []
    }
  },
  methods: {
    // 图片改变事件
    logoChange(e) {
      console.log('图片改变事件');
      console.log(e);
      console.log('文件对象');
      console.log(e.target.files);
      this.dialogVisible.imgFile = e.target.files[0];
      // 将图片转换为url地址
      this.dialogVisible.logoUrl = this.$zero.fileToBlobUrl(e.target.files[0]);
    },

    // 确定修改
    submitChange() {

      this.dialogVisible.loadding = true;
      this.dialogVisible.isUpload = true;

      // 把保存事件发送给父级
      this.$emit('submit');
    },

    /**
     * 关闭弹出层的回调 逻辑
     *  判断当前是否是正在上传保存的信息，如果是，则不隐藏，
     *  如果不是 则隐藏
     */
    beforeClose() {
      console.log('关闭钱的糊掉')
      if (this.dialogVisible.isUpload) {
        console.log('不能关闭');
        return;
      }
      console.log('可以关闭');
      this.dialogVisible.dialogVisible = false;
    },

    // 获取各种类型
    async getType() {
      let company_id = this.$zero.getCompany_id();
      let typeResult = await this.$api.application.getTypeList({
        company_id: company_id
      });
      console.log('获取的类型结果');
      console.log(typeResult);
      if (typeResult.statusCode === 75500) {
        return this.$zero.ToastError('服务繁忙，获取应用分类错误');
      }
      if (typeResult.statusCode !== 75200) {
        return this.$zero.ToastError(`获取应用分类错误，错误码:${typeResult.statusCode}`);
      }

      // 获取成功
      console.log('获取得到的结果');
      console.log(typeResult);
      this.typeList = typeResult.types;
    },
  },

  created() {
    console.log('执行之前');
    console.log(this.dialogVisible);
    this.getType();
  }
}
</script>


<style>
/* 修改应用信息弹窗1 */
.app_detail_update_appinfo .el-dialog__body {
  padding: 0 20px;
}

.app_detail_update_appinfo .el-dialog__footer {
  text-align: center;
}

.app_cover_content {
  padding: 0 20px;
}

.app_cover_content_logo {
  display: flex;
  padding-top: 35px;
}

.app_cover_content_l_des {
  width: 120px;
  color: #999999;
}

.app_cover_content_logowrap {
  position: relative;
  width: 60px;
  height: 60px;
  border: 1px solid #e6e6e6;
  text-align: center;
  line-height: 60px;
}

.app_cover_content_logowrap input {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0;
  cursor: pointer;
}

.app_cover_content_logowrap img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.app_cover_content_des {
  line-height: 60px;
  margin-left: 10px;
  color: #999999;
}

.app_cover_content_name {
  display: flex;
  align-items: center;
  margin-top: 20px;
}

.app_cover_content_name input {
  box-sizing: border-box;
  width: 350px;
  height: 35px;
  padding-left: 8px;
  border: 1px solid #e6e6e6;
}

.app_cover_content_type {
  display: flex;
  align-items: center;
  margin-top: 20px;
  padding-bottom: 40px;
}

/* 修改应用信息弹窗1 */
</style>
