<script>
/*
  应用包中公有的方法
*/
export default {
  methods: {
    /**
     * 上传图片
     * @params {Array} 文件对象
     * 
     * @return {Array} [{format: "jpg"
        server_name: "c9cbf630-c4b6-11e9-b45a-45fcc6062c0e.jpg"
        statusCode: 75600
        upload_name: "1.jpg"}]
     */
    async mixinUploadImg(fileArr) {
      // 获取企业id
      let company_id = this.$zero.getCompany_id();
      if (!company_id) {
        return this.$zero.ToastError("企业id不存在，无法上传应用包");
      }
      let user_id = this.$zero.getUserid();
      if (!user_id) {
        return this.$zero.ToastError("请登录");
      }

      let formData = new FormData();
      for (let i = 0, length = fileArr.length; i < length; i++) {
        formData.append('file', fileArr[i]);
      }
      console.log('图片的东西formData');
      console.log(formData);

      console.log('取出formData');
      console.log(formData.get('file'));

      let uploadResult = await this.$api.file.uploadImg({
        company_id: company_id,
        user_id: user_id,
        file: formData
      });

      if (uploadResult.statusCode === 75500) {
        return this.$zero.ToastError('服务器执行异常,请稍后再试');
      } else if (uploadResult.statusCode === 75601) {
        return this.$zero.ToastError('存储文件出现错误，请重试');
      } else if (uploadResult.statusCode === 75602) {
        return this.$zero.ToastError('不支持的app文件类型,目前支持 ipa apk');
      } else if (uploadResult.statusCode === 75603) {
        return this.$zero.ToastError('files 不存在');
      } else if (uploadResult.statusCode === 75400) {
        return this.$zero.ToastError('文件不存在');
      }

      if (uploadResult.statusCode !== 75200 && uploadResult.statusCode !== 75600) {
        return this.$zero.ToastError(`预览图片上传失败，错误码:${uploadResult.statusCode}`);
      }

      // 上传成功
      console.log('上传成功');
      console.log(uploadResult);

      // this.middleFilesResult.previews = uploadResult.imgStatus; // 存储成功了

      // 将结果返回出来
      if (uploadResult.imgStatus) {
        // 如果这个属性存在 则为多张
        return uploadResult.imgStatus;
      } else {
        // 单张图片
        return [uploadResult]
      }
    },

    /**
     * 通过base64位图片换取地址
     * @params {Object} base64位字符串
     * 
     * @return {Array} [{statusCode: 75200, server_name: "5c0de320-c4a6-11e9-a706-75fbf48657eb.png", upload_name: "5c0de320-c4a6-11e9-a706-75fbf48657eb.png" format: 'png'}]
     */
    async mixinBase64toUrl(base64String) {
      // 获取企业id
      let company_id = this.$zero.getCompany_id();
      if (!company_id) {
        return this.$zero.ToastError("企业id不存在，无法上传应用包");
      }
      let user_id = this.$zero.getUserid();
      if (!user_id) {
        return this.$zero.ToastError("请登录");
      }
      let uploadResult = await this.$api.file.base64toUrl({
        company_id: company_id,
        user_id: user_id,
        base64s: [base64String]
      });

      if (uploadResult.statusCode === 75500) {
        return this.$zero.ToastError('服务繁忙，请稍后再试');
      } else if (uploadResult.statusCode === 75601) {
        return this.$zero.ToastError('存储文件出现错误');
      } else if (uploadResult.statusCode === 75602) {
        return this.$zero.ToastError('不支持的app文件类型,目前支持 ipa apk');
      } else if (uploadResult.statusCode === 75603) {
        return this.$zero.ToastError('files 不存在');
      }

      if (uploadResult.statusCode !== 75200 && uploadResult.statusCode !== 75600) {
        return this.$zero.ToastError(`logo上传失败，错误码${uploadResult.statusCode}`);
      }
      // 上传成功
      return uploadResult.img_result;
    },


    /**
     * 上传应用包换取应用包地址
     * @params {文件对象} 
     * 
     * @return {
          "upload_name": "c24e52a2-8129-44cf-ba61-98e7a855cbb9.ipa",
          "server_name": "dec31ac0-c4af-11e9-8686-75466612dca2.ipa",
          "format": "ipa",
          "statusCode": 75200
      }
     */
    async mixinUploadApp2url(appFile) {
      // 获取企业id
      let company_id = this.$zero.getCompany_id();
      if (!company_id) {
        return this.$zero.ToastError("企业id不存在，无法上传应用包");
      }
      let user_id = this.$zero.getUserid();
      if (!user_id) {
        return this.$zero.ToastError("请登录");
      }

      let fromData = new FormData();
      fromData.append("app_file", appFile);
      console.log("放到formData中的是什么");
      console.log(fromData.get("app_file"));

      let uploadResult = await this.$api.file.uploadAppPackage({
        company_id: company_id,
        user_id: user_id,
        app_file: fromData
      });

      console.log("上传结果");
      console.log(uploadResult);

      if (uploadResult.statusCode === 75500) {
        return this.$zero.ToastError("服务繁忙，请稍后再试");
      } else if (uploadResult.statusCode === 75601) {
        return this.$zero.ToastError("服务错误，存储文件出现错误，请再次上传");
      } else if (uploadResult.statusCode === 75602) {
        return this.$zero.ToastError("不支持的app文件类型，目前支持 ipa apk");
      } else if (uploadResult.statusCode === 75603) {
        return this.$zero.ToastError("files 不存在");
      } else if (uploadResult.statusCode === 75604) {
        return this.$zero.ToastError("files 不存在");
      }

      if (uploadResult.statusCode !== 75200 && uploadResult.statusCode !== 75600) {
        return this.$zero.ToastError(`上传应用包错误，错误码:${uploadResult.statusCode}`);
      }

      // 上传成功
      console.log("上传成功");
      console.log(uploadResult);
      return uploadResult;
    },
  }
}
</script>
